<template>
  <div class="auth">
    <div class="auth-button" @click="redirectToDiscordOAuth">
      ИДЕНТИФИЦИРОВАТЬ ЛИЧНОСТЬ
    </div>
  </div>
</template>

<script>
import { createRequest } from "@/api/requestBuilder";
import requestConfigs from "@/api/requestConfigs";

export default {
  name: "Auth",
  methods: {
    redirectToDiscordOAuth() {
      createRequest(requestConfigs.GETDiscordLink, {
        queryPayload: { redirect_url: window.location.origin + "/" },
      }).then((response) => {
        window.location.href = response.data.url;
      });
    },
  },
  beforeMount() {
    if (this.$store.state.isAuthorized) this.$router.push({ name: "Home" });
  },
};
</script>

<style lang="scss">
.auth-button {
  margin-top: 40px;
  font-size: 25px;
  line-height: 1.2;
  width: min-content;
  border: 4px solid;
  padding: 10px;
  cursor: pointer;

  &:hover {
    color: black;
    user-select: none;
    background-color: white;
  }
}
</style>
